          margin: 15px 0 0 0;
          padding: 0;
          list-style: none;
        }
      }

      &.guide {
        padding-top: 42px;
        padding-bottom: 27px;

        h1 {
          font-size: 18px;
          padding-bottom: 15px;
          margin-top: 30px;
          margin-bottom: 0;
          border-bottom: 1px solid #ccc;
        }

        ul {
          margin: 15px 0 0 0;
          padding: 0;
          list-style: none;

          li {
            margin-bottom: 5px;
          }
        }

        table {
          border-bottom: 1px solid #ddd;

          th {
            border-top: 0;
            border-bottom-width: 1px;
            background: #eee;
          }

          td {
            vertical-align: middle;

            &.desc {
              font-size: 14px;
            }
          }
        }
      }

      &.license {
        padding-top: 42px;
        padding-bottom: 27px;

        .title {
          margin-bottom: 35px;
        }

        .content {
          font-family: initial;

          h1 {
            font-size: 18px;
            padding-bottom: 15px;
            margin-top: 30px;
            margin-bottom: 15px;
            border-bottom: 1px solid #ccc;
          }

          a {
            color: inherit;
          }

          ul {
            margin: 20px -5px 0 -5px;
            padding: 0;
            list-style: none;

            li {
              display: inline-block;
              margin-bottom: 10px;
              padding: 0 5px;

              > a {
                border: 1px solid #ddd;
                display: inline-block;
                padding: 5px 10px;
                text-decoration: none;

                &:hover {
                  color: #fff;
                  background: #146ece;
                  border-color: #146ece;
                }
              }
            }
          }
        }
      }

      &.contact {
        padding-top: 42px;
        padding-bottom: 180px;

        .title {
          margin-bottom: 35px;
        }

        .content {
          > .row {
            > .col {
              min-width: 400px;

              .message {
                margin-bottom: 35px;
              }

              .email {
                margin: 35px 0;
              }

              .satisfaction {
                .icons {
                  margin-top: 25px;

                  i {
                    cursor: pointer;
                    padding: 5px;
                    font-size: 42px;
                    opacity: 0.25;

                    &:hover {
                      opacity: 0.5;
                    }

                    &.active {
                      opacity: 1;
                    }
                  }
                }
              }

              label {
                display: block;
              }

              textarea {
                border-radius: 0;
                height: 500px;
                width: 100%;
                resize: none;
                padding: 15px;
                box-shadow: none;
              }

              input {
                border-radius: 0;
                height: 50px;
                box-shadow: none;
              }

              .action {
                padding-top: 15px;

                button {
                  width: 100%;
                  border-radius: 0;
                  height: 50px;
                }
              }
            }
          }
        }
      }
    }
  }

  > footer {
    border-top: 1px solid #ddd;
    padding: 35px 0;
    background: #f7f7f7;

    > .wrapper:after {
      content: "";
      clear: both;
      display: table;
    }

    .copyright {
      padding: 17.5px 0;
      float: left;
    }
  }

  > .climb {
    position: fixed;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0;
    right: 35px;
    bottom: 15px;
    padding: 0;
    width: 66px;
    height: 66px;
    line-height: 66px;
    text-align: center;
    border: 1px solid;
    box-shadow: none;
    opacity: 0;
    display: none;
    transition: opacity 0.25s;
    z-index: 10;

    &.on {
      opacity: 1;
    }
